import cry from '@/img/cry.svg'

export default {
  props: {
    type: {
      type: String,
      default: ''
    },
    label: {
      type: String,
      default: ''
    },
    button: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      cry
    }
  },
  computed: {
    displayCry () {
      switch (this.type) {
        case 'wish':
          return true
      }
      return false
    }
  },
  methods: {
    click () {
      if (this.button) {
        this.$emit('click')
      }
    }
  }
}



